import './App.scss';
import Navigations from "./navigation/Navigations";
import {useDispatch, useSelector} from "react-redux";
import {useEffect} from "react";
import {addSuggestionFromLocaleStorage, initialSuggestion} from "./store/slices/suggestionSlice";
import {ARRAY_SUGGESTION} from "./data/data";

function App() {
  const dispatch = useDispatch();
  const {productRequests, sortProductRequest} = useSelector(state => state.suggestion);

  useEffect(()=>{
    if(localStorage.getItem('prodReqs')) {
      console.log('test');
      console.log(localStorage.getItem('prodReqs'));
      console.log(JSON.parse(localStorage.getItem('prodReqs')));
      dispatch(addSuggestionFromLocaleStorage(JSON.parse(localStorage.getItem('prodReqs'))));
    }
    else if (sortProductRequest.length === 0) {
      console.log('LOAD DATA');
      dispatch(initialSuggestion());
    }
  },[]);

  useEffect( ()=> {
    console.log('productRequests change');
    if(sortProductRequest.length > 0) {
      console.log('LOCAL STORAGE');
      localStorage.setItem('prodReqs', JSON.stringify(productRequests));
    }
  }, [productRequests])
  return (
    <Navigations />
  );
}

export default App;
